import React from 'react';
import classes from './Footer.module.css';

const Footer = () => {
    return (
        <div className={classes.Footer}>
            <div className={classes.FooterWrapper}>
                <div className={classes.SocialWrap}>

                    <div className={classes.FooterLogo}>
                        <img src="./assets/images/logo_white_full.png"/>
                    </div> 

                    
                    
                    <div className={classes.Shop}>  
                        <h4>Ακολουθήστε μας</h4> 
                        <ul className={classes.Social}>
                            <li><a href="https://www.facebook.com/sokratis.kotopoula"><img src="./assets/icons/facebook.svg"/></a></li>
                        </ul>
                    </div>
                    <div className={classes.Phones}> 
                        <h4>Delivery Service</h4>
                        <div className={classes.PhonesList}>
                            <div><span>Τηλέφωνο</span> <a href="tel:+302463055142">2463055142</a>  </div>
                            <div><span>What's Up</span> <a href="tel:+306988585305">6988585305</a>  </div>
                        </div>                 
                    </div>
                    <div className={classes.Address}> 
                        <a href="https://www.google.com/maps/place/Perdika+6,+Ptolemaida+502+00/@40.511823,21.6761966,17z/data=!3m1!4b1!4m5!3m4!1s0x13577cda87344c79:0xb670484ec6705a22!8m2!3d40.511823!4d21.6783853">
                            <address>Περδίκα 6, Πτολεμαΐδα</address>  
                        </a>  
                    </div>
                    
                </div>
                <div className={classes.Developer}>
                    <p>Web App Developed And Designed by</p>
                    <h6><a href="https://www.instagram.com/yanniotis/">Petros Gianniotis</a></h6>
                </div>    
            </div>          
        </div>
    )
}

export default Footer
