import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import ProductList from '../ProductList/ProductList';
import classes from './Category.module.css';

class Category extends Component {
    componentDidMount() {
        this.props.resetFilter();
        window.scrollTo(0, 0);
        console.log("LOADED : " + this.props.loaded);
        
    }

    componentDidUpdate(prevProps, prevState) {
        window.scrollTo(0, 0);
    }
    
    
    render(){
        console.log("LOADED : " + this.props.loaded);
        
        const index = parseInt(this.props.match.params.id);
        const catIndex = this.props.getCategory(index);
        const cat = this.props.categories[catIndex];
        const products = this.props.getProducts(index);
        console.log("category");
        

        let topNavigation = (
            <div className={classes.TopNavigation}>
                <Link to='/Menu'>
                    <div><img src="./assets/icons/arrow_back.svg" alt="main menu"/></div>   
                </Link>
            </div>
        );

        let cat_filters = null;
        if (this.props.loaded && cat.hasOwnProperty('cat_filters')){
            cat_filters = 
                <div className={classes.CategoryFilters}>
                    
                    <ul> 
                        {cat.cat_filters.map(filter => 
                            <li 
                                onClick={(f) => this.props.setFilter(filter)}
                                className={this.props.filter === filter ? "active" : null}
                                key={filter}>
                                    {filter}
                            </li>)}
                    </ul>
                </div>
        }
        
        let footertext = this.props.loaded && cat.hasOwnProperty("footer") ? cat.footer.split("|") : null;


        let toRender = null;
        if(this.props.loaded){
            toRender = (
                <div className={classes.Category}>
                {topNavigation}
                <h2>{cat.name[0]}</h2> 
                <div className="filters-wrapper">
                    <div className="filters">
                        {cat_filters}
                    </div> 
                </div>
                {cat.hasOwnProperty('header') 
                    ?   <div className={classes.CategoryHeader}>
                            {cat.header}
                        </div> 
                    :   null}

                <ProductList 
                    setFilter = {(filter) => this.props.setFilter(filter)}
                    filter = {this.props.filter}
                    products={products}/> 

                <div className="filters-wrapper">
                    <div className="filters">
                        {cat_filters}
                    </div> 
                </div>    

                {cat.hasOwnProperty('preFooter') 
                    ?   <div className={classes.CategoryPreFooter}>
                            {cat.preFooter}
                        </div> 
                    :   null}
                {cat.hasOwnProperty('footer') 
                    ?   <div className={classes.CategoryFooter}>
                            {footertext.map(txt => (<span>{txt}</span>))}
                        </div> 
                    :   null}
                

                <aside>
                    <p>Στις τιμές συμπεριλαμβάνονται όλοι οι νόμιμοι φόροι.</p>
                    <p>Ο καταναλωτής δεν έχει υποχρέωση να πληρώσει εάν δεν λάβει το Νόμιμο παραστατικό στοιχείο (Απόδειξη ή Τιμολόγιο))</p>
                </aside>
                    
                <div className={classes.Back} >
                    <Link to={`/Category/${cat.nav[0]}`} onClick={() => this.props.navigate(cat.nav[0])}>&lt;</Link>
                    <Link to='/Menu'>
                        Επιστροφή στο κεντρικό μενού
                    </Link>
                    <Link to={`/Category/${cat.nav[1]}`} onClick={() => this.props.navigate(cat.nav[1])}>&gt;</Link>
                </div>  
            </div>
            );
        }
        
        return (
            <div>
              {toRender}  
            </div>
        )
   }
}

export default Category;
