  setupCategories = (data) =>{
    console.log(data)
    const tempCategories = [];
    data.forEach(dt => {
      const info = {};
      info.id = dt.node.postId;  
      info.name = [];
      info.name.push(dt.node.title);
      info.img = dt.node.featuredImage.node.uri;
      info.img = dt.node.featuredImage.node.sourceUrl;
      if(dt.node.category.footer !== null){
        info.footer = dt.node.category.footer;
      }
      info.nav = [];
      tempCategories.push(info);
    });

    const reserved = tempCategories.reverse();
    const size = reserved.length;

    for(let i = 0; i < size; i++){
      console.log("navigation")
      if(i === 0){
        reserved[i].nav.push(reserved[size - 1].id);
        reserved[i].nav.push(reserved[i + 1].id);
      }else if(i === (size - 1)){
        reserved[i].nav.push(reserved[i - 1].id);
        reserved[i].nav.push(reserved[0].id);
      }else{
        reserved[i].nav.push(reserved[i - 1].id);
        reserved[i].nav.push(reserved[i + 1].id);
      }
    }
    
    //console.log(reserved);
    this.setState({categories : reserved, categoriesLoaded : true});
    this.loaded();
  }


  setupProducts = (data) => {
    console.log(data);
    const tempProducts = []; 

    data.forEach(dt => {
      console.log(dt.node.product);
      const info = {};
      if(dt.node.product.catId !== null){
        info.cat_id = dt.node.product.catId.postId;
        if(dt.node.product.subCategory !== null){
          info.sub_category = [];
          info.sub_category.push(dt.node.product.subCategory);
        }
        info.items = [];
        for(let i = 0; i < 5; i++){
          let productTxt = (i === 0) ? "products0" : "products0" + i;
          if(dt.node.product[productTxt].name !== null){
            const item = {};
            item.id = i;
            //console.log(productTxt);
            
            item.name = [];
            item.name.push(dt.node.product[productTxt].name);
            item.price = dt.node.product[productTxt].price;
            if(dt.node.product[productTxt].description !== null) {
              item.description = [];
              item.description.push(dt.node.product[productTxt].description);
            }
            if(dt.node.product[productTxt].info !== null) {
              item.info = [];
              item.info.push(dt.node.product[productTxt].info);
            };
            if(dt.node.product[productTxt].extraPrice !== null){
              item.extraPrice = {};
              item.extraPrice.price = dt.node.product[productTxt].extraPrice;
              item.extraPrice.info = [];
              item.extraPrice.info.push(dt.node.product[productTxt].extraInfo);
            }
            info.items.push(item);
          }
        }
      }
      tempProducts.push(info);
    });

    const reserved = tempProducts.reverse();
    this.setState({products : reserved, productsLoaded : true});
    this.loaded();
  }

  loaded = () =>{
    if(this.state.categoriesLoaded && this.state.productsLoaded){
      this.setState({loaded : true});
    }
  }

