  getProducts = (id) => {
    //console.log("get products" + index);
    let products = [];
    this.state.products.forEach((category, index) => {
      if(category.cat_id === parseInt(id)){
        let itemToPush = {...category, id : index};
        products.push(itemToPush);
      }
    });

    //console.log(products);
    return products;
  }

  getCategory = (id) => {
    return this.state.categories.findIndex(c => c.id === id);
  }

  setFilter = (filter) => {
    if(this.state.filter === filter){
      this.resetFilter();
    }else{
      this.setState({filter : filter});
    }
  }

  resetFilter = () => {
    this.setState({filter : ""});  
  }

  hideIntro = () => {
    const intro = document.querySelector('.intro');
    intro.classList.add('hide');  
  }

  navigate = (index) => {
    this.resetFilter();
    this.setState({navigate : index})
  }
  resetNavigate = () => {
    this.setState({navigate : -1})
  }

